import React from 'react';
import { EditorState, RichUtils } from 'draft-js';
import styled from 'styled-components';
import { FaBold, FaItalic, FaUnderline, FaStrikethrough, FaArrowLeft } from 'react-icons/fa';

const ToolboxText = ({ editorState, onChange }) => {
  const toggleInlineStyle = (style) => {
    onChange(RichUtils.toggleInlineStyle(editorState, style));
  };

  const handleBackClick = () => {
    // Aqu puedes definir la accin para regresar a la pgina anterior
    window.history.back();
  };

  return (
    <ToolBoxContainer>
      <BackButton onClick={handleBackClick}>
        <FaArrowLeft size={20} />
      </BackButton>
      <ToolButtonsContainer>
        <ToolButton onClick={() => toggleInlineStyle('BOLD')}><FaBold size={20} /></ToolButton>
        <ToolButton onClick={() => toggleInlineStyle('ITALIC')}><FaItalic size={20} /></ToolButton>
        <ToolButton onClick={() => toggleInlineStyle('UNDERLINE')}><FaUnderline size={20} /></ToolButton>
        <ToolButton onClick={() => toggleInlineStyle('STRIKETHROUGH')}><FaStrikethrough size={20} /></ToolButton>
        {/* Agrega ms botones segn sea necesario */}
      </ToolButtonsContainer>
    </ToolBoxContainer>
  );
};

const ToolBoxContainer = styled.div`
  display: flex;
  align-items: center; /* Alinea los elementos verticalmente en el centro */
  padding: 0.5rem;
  background-color: #ffffff; /* Fondo blanco */
  border-top: 1px solid #e9ecef; /* Lnea superior de separacin en gris claro */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Sombra ms ligera para destacar el contenedor */
`;

const ToolButtonsContainer = styled.div`
  display: flex;
  justify-content: center; /* Centra los botones horizontalmente */
  flex: 1; /* Ocupa el espacio restante */
`;

const ToolButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  margin: 0 0.5rem;
  color: #4dabf7; /* Color de conos por defecto en azul */
  transition: color 0.3s ease; /* Transicin suave para el cambio de color */

  &:hover {
    color: #3b8fd8; /* Color de conos al pasar el ratn por encima en un tono ms oscuro */
  }
`;

const BackButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  margin-right: 1rem; /* Espacio entre el botn de regresar y los botones de herramientas */
  color: #4dabf7; /* Color del cono de regreso en azul */

  &:hover {
    color: #3b8fd8; /* Color del cono de regreso al pasar el ratn por encima en un tono ms oscuro */
  }
`;

export default ToolboxText;
